<template>
	<view class="content">
		<u-row>
			<u-col span="12" class="tabsList">
				<u-tabs active-color="#fff" inactive-color="#B1D1FF" :list="list" :current="current" @change="change"></u-tabs>
			</u-col>
		</u-row>
		<view style="margin: -120rpx 30rpx 0 30rpx;">
		<scroll-view scroll-y style="height: 800rpx;" v-show="current==0">
			<view class="list01" v-for="(item,index) in list01" :key="index">
				<u-row>
					<u-col span="9">
						<view class="blueline"></view>
						<span>{{item.universalAntiAboutMain26}}</span>
						<p>招募人数：<b>{{item.numberPeople}}</b></p>
						<p>活动时间：<b>{{item.deadlineDate}}</b></p>
						<p>活动地址：<b>{{item.address}}</b></p>
					</u-col>
					<u-col span="3" style="text-align: right;">
						<view style="color:#296DCB;margin-top: -100rpx;">报名中</view>
					</u-col>
					<u-col span="12" style="text-align: right;">
			<!-- 			<u-button v-show="item.status==1" size="mini" class="btn" shape="circle">取消活动</u-button> -->
						<u-button size="mini" class="orgbtn" shape="circle" @click="Signin(item.id)">签到</u-button>
					</u-col>
				</u-row>
			</view>
		</scroll-view>
		<scroll-view scroll-y style="height: 800rpx;" v-show="current==1">
			<view class="list01" v-for="(item,index) in list01" :key="index">
				<u-row>
					<u-col span="9">
						<view class="blueline"></view>
						<span>{{item.universalAntiAboutMain26}}</span>
						<p>招募人数：<b>{{item.numberPeople}}</b></p>
						<p>活动时间：<b>{{item.deadlineDate}}</b></p>
						<p>活动地址：<b>{{item.address}}</b></p>
					</u-col>
					<u-col span="3" style="text-align: right;">
						<view style="color:#296DCB;margin-top: -100rpx;">已结束</view>
					</u-col>
				</u-row>
			</view>
		</scroll-view>
		</view>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		enterAbout,
		registerAbout
	} from '../../api/index.js'
	export default {
		data() {
			return {
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '报名中'
				}, {
					name: '已结束'
				},],
				list01: [],
			}
		},
		onLoad() {
            this.getList();
		},
		methods: {
			Signin(idx){
				registerAbout({userId:uni.getStorageSync('userInfo').userId,'universalAntiAboutFid':idx}).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							title: '签到成功'
						});
					}
				});
			},
			getList(){
				enterAbout({'aboutEnterStatus':(this.current+2)}).then(res => {
					if (res.code == 200) {
						this.list01 = res.data;
					}
				});
			},
			change(index) {
				this.current = index;
			},
			change(index) {
				this.current = index;
				this.getList();
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background-color: #F5F6FA;
	}
    .btn{
		background-color: #296DCB;
		color: #fff;
		font-size: 24rpx;
	}
	.orgbtn{
		background-color: #FB8333;
		color: #fff;
		font-size: 24rpx;
		width: 140rpx;
	}
	.tabsList {
		background-color: #1F61BE;
		min-height: 203rpx;
		border-radius: 0 0 10rpx 10rpx;
	}
	.tabsList /deep/ .u-tabs{
		background: transparent!important;
	}

	.topBox {
		position: relative;
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
	}

	.topBox .ss {
		position: absolute;
		right: -10rpx;
		top: 8rpx;
		width: 30rpx;
		height: 30rpx;
	}

	.grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.list01 {
		width: 100%;
		height: 310rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 20rpx 20rpx 20rpx 30rpx;
		position: relative;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 30rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list01 span,
	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		height: 70rpx;
		display: inline-block;
	}

	.list01 p,
	.list02 p {
		margin-top: 10rpx;
		font-size: 24rpx;
		color: #999999;
		width: 90%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list01 b {
		color: #333;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.listimg {
		width: 208rpx;
		height: 126rpx;
	}

	.list02 {
		width: 100%;
		height: 231rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 17rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>